footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);

}


.footlogo {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}



.footer_logo {
 

    margin-bottom: 2rem;
    height: 10rem;
    width: 10rem;
    position: absolute;
    border-radius: 50%;

}
.footer_logo >img{
    border-radius: 50%;

}
.footer_logo_hover >img{
    border-radius: 50%;

}


.footer_logo_hover {
    margin-bottom: 2rem;
    height: 10rem;
    width: 10rem;
    border-radius: 50%;
}
.footer_logo:hover {
   opacity: 0;
}

.permalinks {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;


}

@media screen and (max-width:600px) {

    .permalinks {
        flex-direction: column;
        gap: 1.5rem;
    }
 
    
    
    
    .footer_logo {
        margin-bottom: 2rem;
        height: 5rem;
        width: 5rem;
    
    }
    .footer_logo_hover {
        margin-bottom: 2rem;
        height: 5rem;
        width: 5rem;
    
    }

}