.home_img{
    background: url(../../assets/me.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 5px var(--color-primary);
    order: 1;
    justify-self: center;
    align-items: center;
    width: 300px;
    height: 300px;
    margin: 2rem auto;
    animation: profile_animate 3s ease-in-out infinite 1s;
}

@keyframes profile_animate{
    0%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    50%{
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }
    100%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
}

.scroll_down{
    position: absolute;
    right:15rem;
    top: 30rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;

}

/* media query */
@media screen and (max-width:1024px){
/* tablet */
    header{
        height:68vh ;
        
    }
    .scroll_down{
        display: none;
    }
    .header_socials{
       
        position: absolute;
        left:5rem;
        top: 25rem;
    
        
    }


}

@media screen and (max-width:600px){
    /* mobile */
    header{
        height:100vh ;
    }
    .scroll_down{
        display: none;
    }
    .header_socials{
       
        position: absolute;
        left:0rem;
        top: 20rem;
        margin-left: 1rem;
    
        
    }
    .me{
        width: 14rem;
        height: 20rem;
        position: absolute;
        left: calc(50% - 7rem);
    }
    .home_img{
        height: 200px;
        width: 200px;
    }


}  