.experince_container{
    display: grid;
    margin: 0 300px;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}
.experince_container > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
.experince_container > div:hover{
    background: transparent;
   border-color:var(--color-white) ;
   cursor: default;
}
.experince_container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);

}
.experince_content{
    margin: 0 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap:2rem;
    
}
.experince_details{
    display: flex;
    gap: .5rem;
    padding:0 40px 0 0;

}
.experince_details-icon{
    margin-top: 6px;
    color: var(--color-primary);

}

/* ===============media query============== */
@media screen and (max-width:1024px) {
    .experince_container{
     grid-template-columns: 1fr;
     margin: 0 150px;
    }
    .experince_container >div{
     width: 80%;
     padding: 2rem;
     margin: 0 auto;

    }
    .experince_content{
        padding: 1rem;
    }
  }
  @media screen and (max-width:600px) {
    
       .experince_container >div{
        width: 100%;
        padding: 2rem 1rem;
        margin: 0 auto;
   
       }
       .experince_content{
           padding: 1rem;
       }
       .experince_container{
        display: grid;
        grid-template-columns: repeat(1,15fr);;
        gap: 2rem;
        width: 80%;
        margin: 0 auto;

    }
    .experince_details{
       
        padding:0 5px 0 0;
    
    }

  }