.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
    

}
#about{
    width: 100%;
    margin: 5rem auto;
    
}
.about_me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
        );
        display: grid;
        margin-top: 4rem;
        place-items: center;
}

.about_me-image{
    border-radius: 2rem;
    overflow: hidden;
    
    transform: rotate(-10deg);
    transition: var(--transition);
height: 100%;
width: 100%;


}

.about_me-image:hover{  
    transform: rotate(0deg);
}

.about_cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1.5rem;
}
.about_card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    width: 12rem;
    transition: var(--transition);
    
}
.about_card:hover{
    background: transparent;
    border-color: var(--color-promary-variant);
    cursor: default;

}
.about_icon{
    color: var(--color-primary);
    font-size: 2rem;
    margin-bottom: 1rem;
}

.about_card h5{
    font-size: 0.9rem;
    
}

.about_content p{
        margin: 2rem 0 2.6rem;
        color: var(--color-light);
    
}
.edu{
    margin: 1rem;
}


/* media query */
@media screen and (max-width:1024px){
    /* tablet */
        .about_container{
            grid-template-columns: 1fr;
            gap: 0;

        }
        .about_me{
            width: 50%;
            
            margin: 2rem auto 4rem;

        }
        .about_content p{
            margin: 1rem 0 1.5rem;
           
        
    }
   
    
    
    
    }
    
    @media screen and (max-width:600px){
        /* mobile */
        .about_me{
            width: 65%;
            
            margin: 0  auto ;

        }
        .about_cards{
            grid-template-columns: repeat(2,0.5fr);
            gap: 2rem;
        }
        .about_content{
            text-align: center;
        }
        .about_content p{
            margin: 1.5rem 0;
        }
        .about_container{
           margin-left: 80px;
       
          
        }
     
     .about_cards{
        display: grid;
        grid-template-columns: repeat(1,1fr);
        gap: 1.5rem;
        
    }
     .about_card{
      width: 100%;
    }
    #about{
        margin-top: 0px;
    }
    
    }